"use client";

import React, { useState, useEffect, useCallback } from 'react';
import { MapContainer, TileLayer, Marker, useMap, useMapEvents } from 'react-leaflet';
import 'leaflet/dist/leaflet.css';
import L from 'leaflet';

// Fix Leaflet icon issue in Next.js
const icon = L.icon({
    iconUrl: "https://unpkg.com/leaflet@1.9.4/dist/images/marker-icon.png",
    iconRetinaUrl: "https://unpkg.com/leaflet@1.9.4/dist/images/marker-icon-2x.png",
    shadowUrl: "https://unpkg.com/leaflet@1.9.4/dist/images/marker-shadow.png",
    iconSize: [25, 41],
    iconAnchor: [12, 41],
});

// Component to handle map events and updates
function MapController({
    onMove,
    center
}: {
    onMove: (lat: number, lon: number) => void,
    center: { lat: number, lon: number }
}) {
    const map = useMap();

    // Update center when prop changes
    useEffect(() => {
        map.setView([center.lat, center.lon], map.getZoom());
    }, [center, map]);

    useMapEvents({
        mousemove: (e) => {
            onMove(e.latlng.lat, e.latlng.lng);
        },
        moveend: () => {
            const c = map.getCenter();
            onMove(c.lat, c.lng);
        }
    });

    return null;
}

export default function MapComponent() {
    const [center, setCenter] = useState({ lat: 37.7749, lon: -122.4194 });
    const [cursorLoc, setCursorLoc] = useState<{ lat: number, lon: number } | null>(null);
    const [address, setAddress] = useState<string>("Hover map to see address");
    const [loading, setLoading] = useState(false);
    const [searchInput, setSearchInput] = useState("");
    const [searchError, setSearchError] = useState("");

    const fetchAddress = useCallback(async (lat: number, lon: number) => {
        try {
            setLoading(true);
            const res = await fetch(`http://localhost:8000/encode?lat=${lat}&lon=${lon}&lang=en`);
            if (res.ok) {
                const data = await res.json();
                setAddress(data.words);
            }
        } catch (e) {
            setAddress("Error connecting to API");
        } finally {
            setLoading(false);
        }
    }, []);

    // Debounce mouse move
    useEffect(() => {
        if (!cursorLoc) return;
        const timer = setTimeout(() => {
            fetchAddress(cursorLoc.lat, cursorLoc.lon);
        }, 100);
        return () => clearTimeout(timer);
    }, [cursorLoc, fetchAddress]);

    const handleSearch = async (e: React.FormEvent) => {
        e.preventDefault();
        setSearchError("");
        const input = searchInput.trim();
        if (!input) return;

        // 1. Try Locates.me Address (Word.Word...)
        if (input.includes('.') && input.split('.').length >= 3) {
            try {
                const res = await fetch(`http://localhost:8000/resolve?address=${input}&lang=en`);
                if (res.ok) {
                    const data = await res.json();
                    setCenter({ lat: data.lat, lon: data.lon });
                    setCursorLoc({ lat: data.lat, lon: data.lon }); // Update address display too
                    return;
                }
            } catch (e) {
                console.error(e);
            }
        }

        // 2. Try Lat, Lon
        if (input.includes(',')) {
            const parts = input.split(',');
            if (parts.length === 2) {
                const lat = parseFloat(parts[0]);
                const lon = parseFloat(parts[1]);
                if (!isNaN(lat) && !isNaN(lon)) {
                    setCenter({ lat, lon });
                    setCursorLoc({ lat, lon });
                    return;
                }
            }
        }

        // 3. Try OSM Nominatim (Place Search)
        try {
            const res = await fetch(`https://nominatim.openstreetmap.org/search?format=json&q=${encodeURIComponent(input)}`);
            if (res.ok) {
                const data = await res.json();
                if (data && data.length > 0) {
                    const lat = parseFloat(data[0].lat);
                    const lon = parseFloat(data[0].lon);
                    setCenter({ lat, lon });
                    setCursorLoc({ lat, lon });
                    return;
                } else {
                    setSearchError("Location not found");
                }
            }
        } catch (e) {
            setSearchError("Search failed");
        }
    };

    return (
        <div className="w-full h-screen relative">
            <MapContainer
                center={[center.lat, center.lon]}
                zoom={14}
                style={{ height: "100%", width: "100%" }}
            >
                <TileLayer
                    attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                    url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                />
                <Marker position={[center.lat, center.lon]} icon={icon} />
                <MapController
                    center={center}
                    onMove={(lat, lon) => setCursorLoc({ lat, lon })}
                />
            </MapContainer>

            {/* Search Bar */}
            <div className="absolute top-4 left-1/2 transform -translate-x-1/2 z-[1000] w-[90%] max-w-md">
                <form onSubmit={handleSearch} className="flex shadow-xl rounded-full overflow-hidden">
                    <input
                        type="text"
                        value={searchInput}
                        onChange={(e) => setSearchInput(e.target.value)}
                        placeholder="Search place, lat,lon, or address..."
                        className="flex-1 px-6 py-3 bg-white text-gray-800 focus:outline-none"
                    />
                    <button type="submit" className="bg-blue-600 text-white px-6 py-3 font-bold hover:bg-blue-700 transition">
                        FIND
                    </button>
                </form>
                {searchError && (
                    <div className="bg-red-500 text-white text-xs px-4 py-1 rounded-full mt-2 text-center shadow-lg inline-block">
                        {searchError}
                    </div>
                )}
            </div>

            {/* Address Display */}
            <div className="absolute bottom-10 left-1/2 transform -translate-x-1/2 z-[1000] bg-white/90 backdrop-blur-md p-6 rounded-2xl shadow-2xl border border-gray-200 text-center min-w-[300px]">
                <h2 className="text-sm uppercase tracking-widest text-gray-500 mb-1">Locates.me Address</h2>
                <div className="text-2xl font-bold text-gray-900 font-mono">
                    {loading ? <span className="opacity-50">...</span> : address}
                </div>
                {cursorLoc && (
                    <div className="text-xs text-gray-400 mt-2">
                        {cursorLoc.lat.toFixed(6)}, {cursorLoc.lon.toFixed(6)}
                    </div>
                )}
            </div>
        </div>
    );
}
